export default {
  name: 'sort',
  data (){
    return {
      sortFs: true,
      cksort: true, //价格
      amount: false, //销量
    }
  },
  methods: {
    //价格排序
    priceClick(){
      this.sortFs = true
      this.amount = false
      this.cksort = !this.cksort
      this.$emit('changePriceClick',this.cksort)
    },
    //销量排序
    amountClick(){
      this.sortFs = false
      this.cksort = false
      this.amount = !this.amount
      this.$emit('changeAmountClick',this.amount)
    }
  },
  mounted (){

  }
}



